import {
  CheckBox,
  Anchor,
  Box,
  Header as Head,
  Menu,
  ResponsiveContext,
} from "grommet";
import { Home, Apps } from "grommet-icons";

const Header = ({ darkMode, setMode }) => {
  const onChange = (event) => setMode(event.target.checked);

  const burgerMenuItems = [
    {
      label: <CheckBox
      label="dark"
      checked={darkMode}
      onChange={onChange}
      toggle
    />,
    },
    // {
    //   label: <Box pad="small">Feedback</Box>,
    //   href: "https://github.com/grommet/grommet/issues",
    // },
  ];

  return (
    <Head background="brand" pad="medium" height="xsmall">
      <Anchor
        margin={{ left: "large" }}
        href="https://conveyanceme.uk/"
        icon={<Home />}
        label="conveyance me"
        color="#1e1d23"
        size="xlarge"
      />
      <ResponsiveContext.Consumer>
        {(size) =>
          size !== "small" ? (
            <Box justify="end">
              <CheckBox
                label="dark"
                checked={darkMode}
                onChange={onChange}
                toggle
              />
            </Box>
          ) : (
            <Menu
              a11yTitle="Navigation Menu"
              dropProps={{ align: { top: "bottom", right: "right" } }}
              icon={<Apps color="#1e1d23"/>}
              items={burgerMenuItems}
              dropBackground="#1e1d23"
              size="medium"
            />
          )
        }
      </ResponsiveContext.Consumer>
    </Head>
  );
};

export default Header;
